.treatmentordercell {
  display: grid; 
  grid-template-columns: 100px 1fr;
  position: relative;
  // align-items: center;
  .cellstyle {
    display: flex; 
    align-items: center;
    // word-wrap: break-word;
    // overflow: hidden;
    // text-overflow: ellipsis;
    // border-left: rgba(238, 238, 238, 1) 1px solid;
    border-collapse: collapse;
    word-wrap: break-word;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .cellstyleCenter {
    display: flex; 
    align-items: center;
    justify-content: center;
    // word-wrap: break-word;
    // overflow: hidden;
    // text-overflow: ellipsis;
    // border-left: rgba(238, 238, 238, 1) 1px solid;
    border-collapse: collapse;
    word-wrap: break-word;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

.textHidden {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  word-break: break-all;
}
