.scheduletodotitle{
    background-color: #71b0e46e;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    height: 36px;
    padding: 0px 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 10px;
}

.scheduletodocell{
    // border: rgba(238,238,238,1) 1px solid;
    // border-collapse: collapse;
    height: 50px;
    display: flex;
    padding-right: 15px;
    padding-left: 5px;
    align-items: center;
    justify-content: space-between;
    &:hover{
        background-color: #DFE5E9;
    }
}

.finishbutton{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 16px;
    border: rgba(245, 166, 35, 1) 1px solid;
    color: rgba(245, 166, 35, 1);
}

.todobutton{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 16px;
    border: rgba(245, 166, 35, 1) 1px solid;
    background-color: rgba(245, 166, 35, 1);
    color: white;
}
