
.axis text {
  font: 5px;
}

.axis path {
  stroke: none;
}

.axis line {
  fill: none;
  stroke: rgba(0, 0, 0, 0.1);
  shape-rendering: crispEdges;
}

.limitaxis text {
  font: 10px;
}

.limitaxis path {
  stroke: none;
}

.limitaxis line {
  fill: none;
  stroke: rgba(0, 0, 0, 0.1);
  shape-rendering: crispEdges;
}

.caption {
  font-weight: normal;
}
