.calendertitle {
  background-color: #71b0e46e;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  height: 36px;
  padding: 0px 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 10px;
}

.calendercontrol {
  display: grid;
  grid-template-columns: 130px 90px;
  grid-column-gap: 10px;
  padding: 10px 0px;
}

.calendercontext {
  height: 130px;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  position: relative;
  cursor: pointer;
}

.calendercontextMonthRows {
  height: 130px;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  position: relative;
}

.calenderdisplayinformation {
  border: #f5a623 1px solid;
  border-radius: 4px;
  height: 400px;
  width: calc(100%  );
  background-color: white;
  position: absolute;
  top: 140px;
  left: 0px;
  z-index: 100;
}

.calenderdisplayinformationnone{
  display: none;
}

.calenderModule{
  display: flex;
  height: 50px;
  justify-content: space-between;
  align-items: center;
  padding: 0px 5px;
  .Modulelayout{
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-column-gap: 10px;
  }
}
