
.nurtiontpntablelayout{
    position: relative;
    &:hover{
        background-color: transparent;
    }
    .nurtionTPNogdercell{
        display: grid;
        grid-template-columns:  1fr 1fr 60px 450px;
        height: 90px;
        .cellfordisposename{
            display: grid;
            grid-template-rows: 40px 50px;
            .cellforitem{
                display: grid;
                grid-template-columns: repeat(8,1fr);
            }
        }
    }
    .cellforNote{
        height: 30px;
        border: rgba(232,232,232,1) 1px solid;
        border-collapse: collapse;
        padding: 0px 5px;
        display: flex;
        align-items: center;
    }
}

.tpncelltextdisplay{
    border: rgba(232,232,232,1) 1px solid;
    border-collapse: collapse;
    padding: 0px 5px;
    display: flex;
    align-items: center;
}
