.prescriptiontablecell {
  display: grid;
  grid-template-columns: 180px 1fr;
  position: relative;
  justify-content: center;
  align-items: center;
}

.prescriptiontablecellcontexttexttime {
  display: flex; //flex
  justify-content: center;
  align-items: center;
  word-wrap: break-word;
  overflow: hidden;
  text-overflow: ellipsis;
  border-left: rgba(238, 238, 238, 1) 1px solid;
  border-collapse: collapse;
}

.prescriptiontablecellcontext {
  display: flex; //flex
  align-items: center;
  border-left: rgba(238, 238, 238, 1) 1px solid;
  border-collapse: collapse;

  .prescriptiontablecellcontexttext {
    display: flex; //flex
    // justify-content: center;
    align-items: center;
    word-wrap: break-word;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
