.wardinfouserlayout {
  border-radius: 4px;
  border: 1px solid rgba(232, 232, 232, 1);
  .wardinfopatient {
    padding: 10px 16px;
    border: 1px rgba(232, 232, 232, 1) solid;
    border-collapse: collapse;
  }
}

.wardblacktoindex {
  font-size: 14px;
  color:  rgba(0,0,196,0.7);
  cursor: pointer;
  text-decoration: underline;
  .wardblacktoindex:hover{
    color :rgb(0,0,196);
  }
}

.wardinfousercontent {
  height: calc(100vh - 250px);
  overflow-y: auto;
}

.wardinfousermodelinfo {
  height: 50px;
  background-color: #cccccc;
  padding: 0px 10px;
  display: flex;
  align-items: center;
}

.wardinfousermodelinfocontext {
  max-height: 200px;
  overflow-y: auto;
  .wardinfousercontextinfo {
    height: 50px;
    border: 1px rgba(238, 238, 238, 1) solid;
    border-collapse: collapse;
    padding: 0px 10px;
    display: flex;
    align-items: center;
  }
}

.wardinfouserflagboard {
  display: grid;
  grid-template-columns: 80px repeat(5, 40px);
  grid-column-gap: 10px;
  .flagtrue {
    width: 100%;
    background-color: #4079b6;
    border-radius: 4px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .flagfalse {
    background-color: white;
    width: 100%;
    border: 1px solid rgba(187, 187, 187, 1);
    border-radius: 4px;
    color: rgba(187, 187, 187, 1);
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
