.controlbarlayout {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  padding-bottom:  10px;
}

.boxshadow {
  box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.5);
  height: 1px;
  width: 100%;
  background-color: rgba(232, 232, 232, 1);
}

.controlmodule_click{
  position: absolute;
  right: 0px;
  top: 50px;
  width: 200px;
  background-color: white;
  box-shadow: 3px 3px 12px;
  z-index: 100px;
}

.controlmodule_unclick{
  display: "none"
}

.controlmoduletitle{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 40px;
}
