.ordertableoneday{
    display: grid;
    grid-template-columns: repeat(24,1fr);
    .cell{
        border: rgba(238,238,238,1) 1px solid;
        border-collapse: collapse;
    }
}

.ordertablethirdday{
    display: grid;
    grid-template-columns: repeat(18,1fr);
    .cell{
        width: 100%;
        border: rgba(238,238,238,1) 1px solid;
        border-collapse: collapse;
    }
}

.ordertableoneweek{
    display: grid;
    grid-template-columns: repeat(21,1fr);
    .cell{
        width: 100%;
        border: rgba(238,238,238,1) 1px solid;
        border-collapse: collapse;
    }
}

.ordertabletwoweek{
    display: grid;
    grid-template-columns: repeat(14,1fr);
    .cell{
        border: rgba(238,238,238,1) 1px solid;
        border-collapse: collapse;
    }
}