.patientinfolayout {
  .patientintitle {
    padding-bottom: 24px;
    border-bottom: rgba(238, 238, 238, 1) 1px solid;
    font-size: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .context {
    padding: 10px 0px;
    display: grid;
    grid-template-columns: 150px 500px;
    .title {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      font-size: 16px;
      font-family: 'PingFangSC-Regular';
    }
    .titlenote {
      display: flex;
      justify-content: flex-end;
      font-size: 16px;
      font-family: 'PingFangSC-Regular';
    }
    .item {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      font-size: 16px;
      font-family: 'PingFangSC-Regular';
    }
  }
}
