.Notelayout {
  padding: 10px;
  height: calc(100vh - 240px);
  overflow-y: auto;
  text-align: left;
}
.topMenu:focus {
  border-radius: 4px;
  background-color: #001a6d88 !important;
  color: white !important;
  border: 1px solid #001a6d88 !important;
  padding-left: 4px;
  padding-right: 4px;
  height: 30px;
}
.leftMenu:focus {
  padding:4px 8px; 
  border: 1px solid rgb(30, 80, 150); 
  cursor: pointer; 
  // text-align: center; 
  border-radius:4px;
  margin-bottom:4px;
  background-color:rgba(30, 80, 150,.1);

  // border: 1px solid rgb(30, 80, 150) !important;
  // background-color:rgba(30, 80, 150,.1);
}


