.calendersubcontextclick {
  display: grid;
  grid-template-rows: 50px 1fr;
  border: 1px #f5a623 solid;
  border-collapse: collapse;
  position: relative;
}
.calendersubcontext {
  display: grid;
  grid-template-rows: 50px 1fr;
  border: 1px rgba(238, 238, 238, 1) solid;
  border-collapse: collapse;
}

.calendersubcontexttitle {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(238, 238, 238, 1);
}
.calendersubcontexttitleclick {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f5a623;
}

.calendersubcontexticonsecond {
  display: grid;
  grid-template-columns: auto auto;
  grid-column-gap: 30px;
}

.calendersubcontexticon {
  display: grid;
  grid-template-rows:1fr 1fr;
  justify-content: center;
  align-items: center;
  .displayinfo{
    color: rgba(238, 238, 238, 1);
  }
}

.calendersubcontexttitletoday {
  font-family: "PingFangSC-Semibold";
}

.triangle{
  width: 0; height: 0;
  border-style: solid ;
  border-width: 17.3px 10px 0 10px;
  border-color: #ffbe59 transparent transparent transparent;
}
