.Wardcardvaluelayout{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 5px;
    .Wardcardvitalvalue{
        display: grid;
        grid-template-columns: 1fr 2fr;
        // grid-column-gap: 5px;
        .valueRows{
            display: grid;
            grid-template-rows: 1fr 1fr;
            grid-row-gap: 5px;
        }
        .bothWordValue{
            display: "block";
            font-weight:600;
            font-size:20px;
            line-height:20px;
        }
    }
}
