.Wardchartlayout{
    display: grid;
    grid-template-columns: 1fr 1fr;
}
.halfwardchart{
    display: grid;
    grid-template-columns: 90px 1fr;
    .singlewardchart{
        display: grid;
        grid-template-rows: auto auto auto;
    }
}

.firstRowTitle{
    display: flex;
    padding-top: 45px;
    justify-content: center;
    align-items: center;
}
.secondRowTitle{
    display: flex;
    padding-top: 10px;
    justify-content: center;
    align-items: center;
}
.thirdRowTitle{
    display: flex;
    padding-top: 10px;
    justify-content: center;
    align-items: center;
}
.rowTitle{
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
