.wardbrdinfomationtitle{
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    background-color:#71b0e46e;
    height: 36px;
    padding: 0px 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.wardbedinformationcontext{
    display: grid;
    grid-template-columns:150px repeat(7,1fr);
    .wardbedinfotablerowtitle{
        height: 50px;
        border: rgba(238,238,238,1) 1px solid;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

.wardbedinfoitemtriangle{
    display: flex;
    justify-content: center;
    align-items: center;
    .triangle{
        height: 0;
        width: 0;
        border-style: solid;
        border-width: 17.3px 10px 0 10px;
        border-color: rgba(255,190,5,1) transparent transparent transparent;
    }
}

.wardbedinfoitemcontext{
    border: #ffbe59 2px solid;
    padding: 10px 10px;
    border-radius: 4px;
    .wardbedinfoitemcontextgrid{
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 5px;
        .wardbedinfoitemcontexttitle{
            background-color: #ffe2c6;
            height: 30px;
            padding: 0px 10px;
            display: flex;
            justify-content: center;
            align-items: center;
            color: rgba(0,0,0,.7);
        }
        .wardbedinfoitemcontextgridlayout{
            overflow-y: auto;
            max-height: 200px;
            min-height: 50px;
            .nopatient{
                display: grid;
                align-items: center;
                justify-content: center;
                height: 35px;
                font-size: 20px;
            }
            .wardbedinfoitemcontextgridRow{
                height: 35px;
                display: grid;
                grid-template-columns: 1fr 3fr;
            }
            .wardbedinfoitemcontextgridRowStyle{
                padding: 0px 10px;
                display: flex;
                align-items: center;
                border: rgba(238,238,238,1) 1px solid;

            }
        }
    }
}
