.remark{
    position: relative; 
    // height: 100px;
    &:hover{
        background-color: transparent;
    }
}

.nurtionfeedingcell{
    &:hover{
        background-color: transparent;
    }
    .cellforstate{
        display: grid;
        grid-template-columns:  1fr 1fr 2fr;
        .celllayout{
            border: rgba(232,232,232,1) 1px solid;
            border-collapse: collapse;
            padding: 0px 5px;
            display: flex;
            align-items: center;
            height: 50px;
        }
    }
    .cellforremark{
        border: rgba(232,232,232,1) 1px solid;
        border-collapse: collapse;
        padding: 0px 5px;
        display: flex;
        align-items: center;
        height: 50px;
        font-weight: 600;
        color: rgb(0,120,0); 
    }
}
