.warscardstatusgrid {
  display: grid;
  grid-template-columns: 40px 40px 40px;
  grid-template-rows: 1fr 1fr 1fr;
  grid-gap: 4px;
}

.wardinfouserstatusgrid {
  display: grid;
  grid-template-columns: 60px repeat(8, 40px);
  grid-column-gap: 2px;
}
.gridtwocoltwo {
  grid-column: 1 / span 2;
}

.gridtwocolone {
}

.statustrue {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  background-color: rgba(64, 121, 182, 1);
  color: white;
  border-radius: 4px;
  height: 20px;
}
.statusfalse {
  border: rgba(187, 187, 187, 1) 1px solid;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(187, 187, 187, 1);
  width: 100%;
  height: 20px;
}
