.Chronicvitalchary{
    display: grid;
    grid-template-rows: repeat(6,60px);
    position: relative;
    .grid{
        position: absolute;
        top: 5;
        left: 50;
        height: 360;
    }
}

.chronicvitalCardTitle{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100px;
    overflow: hidden;
    padding: 6px;
    margin: 5px 0;
}

.chronicvitalCardTitleText{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.chronicvitalCardTitleVal{
    display: flex;
    flex-direction: column;
    align-items: center;
}