.ScheduleEventLayout {
  // border: rgba(238, 238, 238, 1) 1px solid;
  // border-radius: 4px;
  .ScheduleEventTitle {
    height: 36px;
    background-color: #71b0e46e;
    padding: 0px 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 4px 4px 0 0;
  }
  .ScheduleEventContext {
    border: 1px solid rgba(232,232,232,1);
    border-radius: 0 0 4px 4px;
    border-collapse: collapse;
    padding: 0px 16px;
    .ContextControl {
      height: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
      .buttonGroup {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-column-gap: 5px;
        .buttonselect {
          display: flex;
          justify-content: center;
          align-items: center;
          border-radius: 16px;
          padding: 0px 20px;
          background-color: rgba(245, 166, 35, 1);
          cursor: pointer;
          .titlecolor {
            color: white;
          }
        }
        .buttonUnselect {
          display: flex;
          justify-content: center;
          align-items: center;
          border-radius: 16px;
          padding: 0px 16px;
          border: rgba(245, 166, 35, 1) 1px solid;
          cursor: pointer;
          .titlecolor {
            color: rgba(245, 166, 35, 1);
          }
        }
      }
    }
    .ContextList {
      max-height: 300px;
      min-height: 120px;
      overflow-y: auto;
      .CellLayout {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0px 5px;
        height: 35px;
        .Context {
          display: grid;
          grid-template-columns: 60px 100px 50px 1fr;
          grid-column-gap: 10px;
        }
      }
    }
  }
}
