.wardnotetitle {
  background-color: #71b0e46e;
  height: 36px;
  padding: 0px 16px;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.wardnotecontextonedit {
  max-height: 200px;
  overflow-y: auto;
  min-height: 50px;
}
.wardnotecontextdisedit {
  max-height: 300px;
  overflow-y: auto;
  min-height: 50px;
}

.announcelistcontext {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0px 16px;
  height: 50px;
  border: rgba(238,238,238,1) 1px solid;
  border-collapse: collapse;
  &:hover {
    background-color: #dfe5e9;
  }
}
