.wardcarddetileditbutton {
  height: 25px;
  width: 25px;
  border-radius: 99em;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(59, 151, 225, 0.5);
  &:hover {
    background-color: rgba(59, 151, 225, 1);
  }
}

.wardinforeportgroup{
  display: flex; 
  justify-content: space-between;
  align-items: center;
  width: 70px;
}

.wardinfoweeks{
  height: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 10px;
  border-bottom: 0.5px solid rgba(215, 238, 255, 1);
}

.wardcarddetailtitle{
  height: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(215, 238, 255, 1);
  padding: 9px 10px; 
}

.wardcarddetailinfo{
  height: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 10px;
  border-bottom: 0.5px solid rgba(215, 238, 255, 1); 
}
.wardcarddetailborderclick {
  border-radius: 4px;
  border: 1px solid rgba(245, 166, 35, 1);
}

.wardcarddetailborderunclick {
  border-radius: 4px;
  border: 1px solid rgba(215, 238, 255, 1);
}

.patientviewbutton {
  height: 22px;
  border-radius: 4px;
  background-color: rgba(59, 151, 225, 0.5);
  // box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.5);
  position: absolute;
  right: 8px;
  color:rgba(0,0,0,0.6) ;
  padding: 0px 4px;
  z-index: 100;
  display: flex;
  align-items: center;
  &:hover {
    background-color: rgba(59, 151, 225, 1);
    color: white;
  }
}

.modulecontent{
  display: grid;
  grid-template-columns: 69px 136px; 
  grid-column-gap: 10px;
  .sparetext{
    text-align: justify;
  }
}

.WardcardModuleDeleteBtn{
  border: 1px solid rgba(208,2,27,1);
  color: rgba(208,2,27,1);
}

.WardcardModuleDeleteBtnDisable{
  border: 1px solid rgba(208,2,27,1);
  color: rgba(208,2,27,1);
}
